import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { ExerciseTextPage } from '../exercise-text/exercise-text';
import { ExerciseImagesPage } from '../exercise-images/exercise-images';
import { DataProvider } from '../../providers/data/data';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  constructor(public navCtrl: NavController, public dataProvider: DataProvider) {

  }

  goToAnimals() {
    this.goToImagePage(this.dataProvider.TYPE.ANIMALS);
  }

  goToColours() {
    this.goToImagePage(this.dataProvider.TYPE.COLOURS);
  }

  goToFood() {
    this.goToImagePage(this.dataProvider.TYPE.FOOD);
  }

  goToFruits() {
    this.goToImagePage(this.dataProvider.TYPE.FRUIT);
  }

  goToMaths() {
    this.goToTextPage(this.dataProvider.TYPE.MATHS);
  }

  goToObjects() {
    this.goToImagePage(this.dataProvider.TYPE.OBJECTS);
  }

  // Push pages
  goToImagePage(type) {
    this.navCtrl.push(ExerciseImagesPage, { type: type });
  }

  goToTextPage(type) {
    this.navCtrl.push(ExerciseTextPage, { type: type });
  }
}



