import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ExerciseQuestion } from '../../model/ExerciseQuestion';
import * as animals from "../../assets/data/animals.json";
import * as colours from "../../assets/data/colours.json";
import * as food from "../../assets/data/food.json";
import * as fruits from "../../assets/data/fruits.json";
import * as objects from "../../assets/data/objects.json";
import { findIndex, shuffle } from 'lodash';

@Injectable()
export class DataProvider {

  TYPE = {
    ANIMALS: 'ANIMALS',
    COLOURS: 'COLOURS',
    FOOD: 'FOOD',
    FRUIT: 'FRUITS',
    OBJECTS: 'OBJECTS',
    MATHS: 'MATHS'
  }

  OPERATIONS = [{
    SIGN: '+',
    method: function (a, b) { return a + b; }
  }, {
    SIGN: '-',
    method: function (a, b) { return a - b; }
  }];

  OPERATION_LIMITS = {
    MIN: 1,
    MAX: 10
  }
  animalCollection = [];
  colourCollection = [];
  foodCollection = [];
  fruitCollection = [];
  objectCollection = [];

  constructor(public http: HttpClient) {
    console.log('Hello DataProvider Provider');
    this.animalCollection = (animals as any).animals;
    this.colourCollection = (colours as any).colours;
    this.foodCollection = (food as any).food;
    this.fruitCollection = (fruits as any).fruits;
    this.objectCollection = (objects as any).objects;
  }

  generateQuestion(type) {

    let arrayOfValues;
    switch (type) {
      case this.TYPE.ANIMALS:
        arrayOfValues = this.animalCollection;
        break;
      case this.TYPE.COLOURS:
        arrayOfValues = this.colourCollection;
        break;
      case this.TYPE.FOOD:
        arrayOfValues = this.foodCollection;
        break;
      case this.TYPE.FRUIT:
        arrayOfValues = this.fruitCollection;
        break;
      case this.TYPE.OBJECTS:
        arrayOfValues = this.objectCollection;
        break;
      default:
        break;
    }

    let objectOptions = [];

    let stringOptions = (shuffle(arrayOfValues)).slice(0, 4);
    for (let stringOption of stringOptions) {
      objectOptions.push({ value: stringOption, visible: true });
    }

    let resultOption = objectOptions[Math.floor(Math.random() * objectOptions.length)];

    return new ExerciseQuestion(resultOption.value, objectOptions, resultOption.value);
  }

  generateMathQuestion() {
    let mathOptions = [];

    const selectedOperatorIndex = Math.floor(Math.random() * this.OPERATIONS.length);
    let selectedOperator = this.OPERATIONS[selectedOperatorIndex].SIGN;

    let a = this.randomNumber(this.OPERATION_LIMITS.MAX - 1);
    let b;
    if (selectedOperator === '+') {
      b = this.randomNumber(this.OPERATION_LIMITS.MAX - a);
    } else if (selectedOperator === '-') {
      b = this.randomNumber(a - 1);
    }
    let result = this.OPERATIONS[selectedOperatorIndex].method(a, b);

    mathOptions.push({ value: result, visible: true });
    while (mathOptions.length < 4) {
      const randomNumber = this.randomNumber(this.OPERATION_LIMITS.MAX);
      if (findIndex(mathOptions, { value: randomNumber }) === -1) {
        mathOptions.push({ value: randomNumber, visible: true });
      }
    }
    mathOptions = shuffle(mathOptions);
    return new ExerciseQuestion(`${a} ${selectedOperator} ${b}`, mathOptions, result);
  }

  randomNumber(max) {
    return Math.floor((Math.random() * max) + 1);
  }

}



