import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { DataProvider } from '../../providers/data/data';
import { HomePage } from '../home/home';

@IonicPage()
@Component({
  selector: 'page-exercise-text',
  templateUrl: 'exercise-text.html',
})
export class ExerciseTextPage {

  correct;
  incorrect;
  type;
  question;

  constructor(public navCtrl: NavController, public navParams: NavParams, public dataProvider: DataProvider) {
    this.type = this.navParams.get('type');
    this.init();
  }

  init() {
    this.correct = false;
    this.incorrect = false;

    switch (this.type) {
      case this.dataProvider.TYPE.MATHS:
        this.question = this.dataProvider.generateMathQuestion();
        break;
      default:
        break;
    }
  }

  isCorrect(position) {
    if (this.question.options[position].value === this.question.result) {
      this.correct = true;
      this.incorrect = false;

      for (let optionIncorrect of this.question.options) {
        optionIncorrect.visible = false;
      }

    } else {
      this.question.options[position].visible = false
      this.correct = false;
      this.incorrect = true;
    }
  }

  disableOption(position) {
    return !this.question.options[position].visible ? true : false
  }

  getOption(position) {
    return this.question.options[position].value ;
  }

  back() {
    this.navCtrl.push(HomePage);
  }
}



